import React from "react"
import SEO from "../../components/seo"
import Sidebar from "../../components/sidebar"
import { Link, graphql } from "gatsby"

export default ({ data }) => (
  <div className="wrapper">
    <SEO title="Blog" keywords={[`blog`, `juan latorre`, `developer`]} />
    <Sidebar />
    <section className="blog-section">
      <h2 className="title is-size-4">¿Qué hay de nuevo?</h2>
      <div className="columns is-multiline pl-3 pr-3">
      {data.allMarkdownRemark.edges.map(({ node }) => (
        <div className="column is-12" key={node.id}>
          <p>{node.frontmatter.date}</p>
          <h4 className="title is-size-5 postTitle">{node.frontmatter.title}</h4>
          <p>{node.excerpt}</p>
          <Link to={node.fields.slug}>Ver más</Link>
        </div>
      ))}
      </div>
    </section>
  </div>
)

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY", locale: "es")
          }
          excerpt,
          fields {
            slug
          }
        }
      }
    }
  }
`